/*Navbar Styling Starts from Here*/
.navbar{
  background: #006BE6;
}

.navbar-brand {
  font-family: Pusia-DemiBold;
  font-size: 18px;
  line-height: 21px;
}

.nav-left{
  font-family: Pusia-DemiBold;
  font-size: 18px;
  line-height: 21px;
}


.nav-right{
  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
}

.nav-item a{
  color: white !important;
}

.active{
  color: #FDC535;
  background: #1A85FF;
  border-radius: 6px 6px 0px 0px;
}

/* Sign up styling starts here */

.modal-content{
  border-radius:  15px;
  border: 0px;
  outline: none;
}

.modal-backdrop {
  background-color: #003c80 !important;
}

.modal-header{
  text-align: right !import;
}
.close:hover{
  cursor: pointer !import;
}

.close{
   position: absolute;
   right: 4px;
   top: 4px;
}
.already-sign-in{
    font-family: 'Gilmer Bold';
    font-size: 14px;
    line-height: 17px;
    font-style: normal;
    font-weight: 900;
    letter-spacing: 0em;
    color: #374B58;
}

.logo-wrapper{
  display: flex;
}


.already-sign-in p{

}

.signin{
   text-decoration: none !important;
}

.sign-up{
  
  color: #374B58;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: 29px;
  letter-spacing: 0px;
  font-family: 'Gilmer Heavy'; 

}

.form-group {
   margin: 24px 0px;
}


.form-field{
  border-radius:  10px;
  height: 60px;
  font-family: Gilmer Regular;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 17px;
  letter-spacing: 0px;
  text-align: left;
  color: #8897A0;
 ;
}

.password-notes{
  font-family: Gilmer Medium;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 14px;
  letter-spacing: 0em;
  color: #8897A0;
  margin-left: 1px;
}

.form-check-label{
  font-family: Gilmer Medium;
  font-size: 10px;
  font-style: normal;
  font-weight: 600;
  line-height: 12px;
  letter-spacing: 0px;
  text-align: left;
  color: #8897A0;

}

.submit{
  border-radius:  10px;
  height: 60px;
  background-color:#0075FA;
  border: 1px solid #A5B0B74D;
  font-family: Gilmer Bold;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
  letter-spacing: 0px;
  text-align: center;
  color: white;

}

input.form-control, input.form-control:focus, select.form-select{
    box-shadow: none;
   -webkit-box-shadow: none;
   -moz-box-shadow: none;
   -moz-transition: none;
   -webkit-transition: none;
   color: #8897A0;

}

button.form-control{
    box-shadow: none !impotant;
   -webkit-box-shadow: none !important;
   -moz-box-shadow: none !impotant;
   -moz-transition: none !impotant;
   -webkit-transition: none !impotant;
}

button.btn {
    box-shadow: none !impotant;
   -webkit-box-shadow: none !important;
   -moz-box-shadow: none !impotant;
   -moz-transition: none !impotant;
   -webkit-transition: none !impotant;
}

select:focus {
  outline: 0;
  box-shadow: none !important;
  color: #8897A0;
}

::placeholder { 
 color: #8897A0 !important;
  opacity: 1; 
}

:-ms-input-placeholder {
  color: #8897A0 !important;;
}

::-ms-input-placeholder {
  color: #8897A0 !important;
}

@media(max-width: 800px){
  .textlogo{
    margin: 0px !important;
  }

  .already-sign-in-wrapper{
    margin: 0px !important;
  }
}

