/* Category Section*/
.category-section{
   padding :31px 0px;
   box-shadow: 0px 0px 5px rgba(19, 129, 255, 0.2);
}

.section-heading{
  font-family: Gilmer Bold;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0px;
  text-align: left;
  color: #374B58;

}

.categories{
  margin-top: 33px;
  display: flex;
  flex-wrap: wrap;
} 

.category{
  padding: 10px 10px;
  margin: 10px;
}


.category a{
  text-decoration: none;
}

.category-image{
  display: block;
  margin: auto;
} 

.cat-wrapper{
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 10px;
}

.category-caption{
  font-family: Gilmer Medium;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #374B58;
  margin-top: 10px;

}
